import styled from "styled-components";
import { useProgress } from "../../../contexts/ProgressContext";
import { useSizeRatio } from "../../../hooks/useSizeRatio";
import { Block } from "../Block";
import { Modal } from "./Modal";
import { Button } from "../Button";
import { useState } from "react";

const Content = styled(Block)`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
`;

const ButtonStyled = styled(Button)`
    margin-top: var(--spacing_x5);
`;

const ButtonsWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    & button {
        width: calc((100% - var(--spacing_x3) + (var(--spacing_x1) / 2))/2);
    }
`;

const ProgressWrapper = styled.div`
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: var(--spacing_x5) 0;
`;

const ProgressCircle  = styled.div`
    width: ${({$ratio}) => $ratio * 7}px;
    height: ${({$ratio}) => $ratio * 7}px;
    border-radius: 50%;
    background-color: var(--color-${({$isActive}) => $isActive ? 'green2' : 'green'});
    opacity: ${({$isActive}) => $isActive ? 1 : 0.3};
    transition: background-color 0.2s;

    & + & {
        margin-left:  ${({$ratio}) => $ratio * 7}px;
    }
`;

export const PrizesModal = () => {
    const ratio = useSizeRatio();
    const [part, setPart] = useState(0);
    const { user, setModal } = useProgress();
    const amount = 3;
    const progress = Array.from({length: amount}, (v, i) => i);

    const Progress = () => (
        <ProgressWrapper>
            {progress.map((p) => (
                <ProgressCircle key={p} $isActive={p === part} $ratio={ratio}/>
            ))}
        </ProgressWrapper>
    );

    const getContent = () => {
        let content;
        switch (part) {
            case 0: 
                content = (
                    <p>
                        <b> ,   :</b>{'\n\n'}
                        ,  ,  <b> </b>    
                        <b> </b>.     
                        3.
                    </p>
                );
                break;

            case 1: 
                content = (
                    <p>
                        <b>   </b>     .
                        {'\n\n'}
                         {'\n'}
                         ,   <b></b>{'\n'}
                        <b>20</b>.   {'\n'}
                        <b>4 </b>.
                    </p>
                )
                break;
            case 2: 
                content = (
                    <p>
                         {'\n'}<b>Telegram-</b>.     {' '}
                       <b>   </b>,   . {'\n\n'}
                       <b></b>:{'\n'}
                             !
                    </p>
                )
                break;

            default: break;
        }

        return (
            <Content isWhite>
                {content}
                <Progress />
                {part === 0 ? (
                    <Button onClick={() => setPart(prev => prev + 1)}></Button>
                ) : (
                    <ButtonsWrapper>
                        <Button color="green2" onClick={() => setPart(prev => prev - 1)}></Button>
                        <Button onClick={() => part === 2 ? setModal({visible: false}) : setPart(prev => prev + 1)}></Button>
                    </ButtonsWrapper>
                )}
                
            </Content>
        )
    }

    return (
        <Modal isDarken isDisabledAnimation>
            {user?.isVip ? getContent() : (
                <Content isWhite>
                    <p>
                        <b> ,   :</b>{'\n\n'}
                        ,  ,  <b> </b>     
                         <b> </b>.     
                        3.{'\n\n'}
                           <b>4  </b>.{'\n'} 
                              ,  <b>75</b>.
                    </p>
                    <ButtonStyled onClick={() => setModal({visible: false})}></ButtonStyled>
                </Content>
            )}
        </Modal>
    )
};
